import { FETCH_BLOGS, FETCH_BLOG } from '../actions';

const initialState = { blogs: [], blog: null }
export default (state = initialState, action) => {
    switch (action.type) {
        case FETCH_BLOGS:
            return {...state, blogs: action.payload.data};
        case FETCH_BLOG:
            return {...state, blog: action.payload.data};
        default:
            return state
    }
}