import React, { Component } from "react";
import "./blog-list.css";

class BlogList extends Component {
    blogs = [{
        id: "1",
        title: "Blog Test",
        detail: "Blog Test Detail",
        date: ""
    },
    {
        id: "2",
        title: "Blog Test",
        detail: "Blog Test Detail",
        date: ""
    }];

    renderBlogs() {
        return this.blogs.map((blog) => {
            return (
                <div key={blog.id} className="row blog">
                    <span className="col-md-2">
                        {blog.id}
                    </span>
                    <span className="col-md-10">
                        {blog.title}
                    </span>
                </div>
            );
        });
    }

    render() {
        return (
            <div>
                {this.renderBlogs()}
            </div>
        );
    }
}

export default BlogList;